
.pswp__scroll-wrap .pswp__ui .pswp__top-bar {
  background: none;
}
.pswp__scroll-wrap .pswp__ui .pswp__top-bar .pswp__counter {
  opacity: 0;
}
.pswp__scroll-wrap .pswp__ui .pswp__top-bar .pswp__button--fs {
  display: none;
}
.pswp__scroll-wrap .pswp__ui .pswp__top-bar .pswp__button--zoom {
  display: none;
}
.pswp__scroll-wrap .pswp__ui .pswp__top-bar .pswp__button--close {
  float: left;
}
.pswp__scroll-wrap .pswp__ui .pswp__button--arrow--left {
  background: none;
  opacity: 1;
}
.pswp__scroll-wrap .pswp__ui .pswp__button--arrow--left:before {
  width: 30px;
  height: 60px;
  background: url("../assets/btn-pre.png") no-repeat center center;
  background-size: 100% 100%;
}
.pswp__scroll-wrap .pswp__ui .pswp__button--arrow--right {
  background: none;
  opacity: 1;
  visibility: visible;
}
.pswp__scroll-wrap .pswp__ui .pswp__button--arrow--right:before {
  width: 30px;
  height: 60px;
  background: url("../assets/btn-next.png") no-repeat center center;
  background-size: 100% 100%;
}
.pswp .pswp__img {
  max-width: 100%;
}


