
@import "../../common/style.css";
body,
html {
  position: relative;
  z-index: -2;
}
body #app,
html #app {
  width: 375px;
  min-height: 100%;
  background: url("../../assets/detail_bg.jpg") no-repeat center top;
  background-size: cover;
  margin: 0 auto;
  position: relative;
  z-index: -2;
}
body #app .longModule,
html #app .longModule {
  width: 375px;
  margin: 0 auto;
  position: relative;
  z-index: -2;
  background: #fff;
  min-height: 100%;
  background-size: 100% auto;
}
body #app .userMes div,
html #app .userMes div {
  margin: 0 5px;
  line-height: 25px;
}
body #app .userMes .userImg img,
html #app .userMes .userImg img {
  width: 25px;
  height: 25px;
  border-radius: 50%;
}
body #app .part,
html #app .part {
  border-radius: 6px;
  padding: 10px;
  min-height: 332px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  background-size: 100% auto;
}
body #app img,
html #app img {
  margin: 0 auto;
  max-width: 100%;
}
body #app img[lazy=loading],
html #app img[lazy=loading] {
  width: auto;
  -webkit-animation: bganim 2s linear 0s infinite;
          animation: bganim 2s linear 0s infinite;
}
body #app .text_part,
html #app .text_part {
  padding: 7px 0;
  line-height: 1.5em;
  word-wrap: break-word;
  overflow: hidden;
  text-align: justify;
}
body #app header,
html #app header {
  width: 100%;
}
body #app .bgTop,
html #app .bgTop {
  z-index: -1;
}
body #app .mask,
html #app .mask {
  position: fixed;
  width: 376px;
/*加渐变后少一像素,在这里补上*/
  height: 129px;
  background: -moz-linear-gradient(rgba(2,2,2,0.5), rgba(85,85,85,0));
  z-index: 99;
}
body #app .letterModule,
html #app .letterModule {
  padding: 10px 0;
}
body #app .letterModule .moduleBox .moduleContent,
html #app .letterModule .moduleBox .moduleContent {
  padding: 5px 20px;
  overflow: hidden;
}
@-webkit-keyframes bganim {
from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
@keyframes bganim {
from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
body .saveImg,
html .saveImg {
  display: none;
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 48px;
  padding-top: 10px;
  z-index: 2000;
  background: #000 url("../../assets/saveImg.png") no-repeat center center;
  background-size: 150px auto;
}
body #music,
html #music {
  position: fixed;
  right: 5px;
  top: 10px;
  width: 40px;
  height: 40px;
  z-index: 100;
}
body #music .music,
html #music .music {
  position: absolute;
  width: 40px;
  height: 40px;
  background: url("../../assets/playOrPause.png") no-repeat 0 0;
  background-size: auto 100%;
}
body #music .music.play,
html #music .music.play {
  -webkit-animation: move 2s steps(360) infinite;
  animation: move 2s steps(360) infinite;
  -moz-animation: move 2s steps(360) infinite;
}
body #music .music.pause,
html #music .music.pause {
  background: url("../../assets/playOrPause.png") no-repeat -40px 0;
  background-size: auto 100%;
}
@-webkit-keyframes move {
0% {
    -webkit-transform: rotate(0deg);
}
50% {
    -webkit-transform: rotate(180deg);
}
100% {
    -webkit-transform: rotate(360deg);
}
}
@-webkit-keyframes move {
0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
50% {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
}
100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
@keyframes move {
0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
50% {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
}
100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}


